@media (min-width: 996px) {
  .omb_row-sm-offset-2 > div:first-child[class*="col-"] {
    margin-left: 16.66%;
  }
}

.omb_login {
  .omb_authTitle {
    text-align: center;
    line-height: 300%;
  }
  .omb_socialButtons a {
    color: white; // In yourUse @body-bg
    opacity:0.9;

    &:hover {
      color: white;
      opacity:1;
    }
    &.omb_btn-facebook {background: #3b5998;}
    &.omb_btn-twitter {background: #00aced;}
    &.omb_btn-google {background: #c32f10;}
    &.omb_btn-github {background: #333333;}
  }
  .omb_loginOr {
    position: relative;
    font-size: 1.5em;
    color: #aaa;
    margin-top: 1em;
    margin-bottom: 1em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;

    .omb_hrOr {
      background-color: #cdcdcd;
      height: 1px;
      margin-top: 0px !important;
      margin-bottom: 0px !important;
    }
    .omb_spanOr {
      display: block;
      position: absolute;
      left: 50%;
      top: -0.6em;
      margin-left: -1.5em;
      background-color: white;
      width: 3em;
      text-align: center;
    }
  }
  .omb_loginForm {
    .input-group.i {
      width: 2em;
    }
    .help-block {
      color: red;
    }
  }
  .input-group input {
    &[type="text"], &[type="password"], &[type="email"], &[type="file"] {
      height: 50px;
    }
  }
  span.input-group-addon .fa {
    width: 24px;
    text-align: center;
  }
  .omb_forgotPwd {
    text-align: right;
    margin-top:10px;
  }
}